import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import routeConstant from 'routes/route/routeConstant';
import { Link } from 'react-router-dom';
import { BtnAddNew } from 'components/common/button';
import { ColEditDelete } from 'components/common';
import { Card, Row, Table, Col, message } from 'antd';
import { getListPaperType, deletePaperType } from 'services/PaperType';

class PaperType extends Component {
  state = {
    dataTable: [],
    pagination: {},
    loading: false,
    IsPagingEnabled: true,
  };

  componentDidMount() {
    this.getListPaperTypetable();
  }

  getListPaperTypetable = (page, IsPagingEnabled) => {
    this.setState({
      loading: true,
    });
    getListPaperType(page, IsPagingEnabled)
      .then(res => {
        const pagination = { ...this.state.pagination };
        const { items } = res.data;
        pagination.total = res.data.totalResults;
        items.forEach(item => {
          if (item.typeId === 1) {
            item.typeId = 'Giấy tờ thường';
          } else {
            item.typeId = 'Nhập học';
          }
        });
        this.setState({
          dataTable: res.data.items,
          pagination,
          loading: false,
        });
      })
      .catch(error => {
        console.log('====>', error);
        this.setState({
          loading: false,
        });
      });
  };

  handleTableChange = pagination => {
    const { IsPagingEnabled } = this.state;
    this.getListPaperTypetable(pagination.current, IsPagingEnabled);
  };

  confirmDelete = e => {
    this.setState({
      loading: true,
    });
    deletePaperType(e)
      .then(() => {
        message.success('Xóa giấy tờ thành công');
        this.getListPaperTypetable();
        this.setState({
          loading: false,
        });
      })
      .catch(error => {
        console.log('res', error);
        message.error(error.response.data.message);
        this.setState({
          loading: false,
        });
      });
  };

  cancel = () => {};

  renderTable = () => {
    const { dataTable } = this.state;
    const columns = [
      {
        title: 'Ký hiệu',
        dataIndex: 'id',
        key: 'id',
      },
      {
        title: 'Tên loại giấy tờ',
        dataIndex: 'label',
        key: 'label',
      },
      {
        title: 'Loại giấy tờ',
        dataIndex: 'typeId',
        key: 'typeId',
      },
      {
        title: 'Thao tác',
        dataIndex: 'operation',
        render: (text, record) =>
          dataTable.length >= 1 ? (
            <Fragment>
              <ColEditDelete
                linkEdit={{
                  to: {
                    pathname: routeConstant.DANH_MUC__TUYEN_SINH__LOAI_GIAY_TO__NHAP_DU_LIEU,
                    search: `?id=${record.id}`,
                  },
                }}
                onConfirmDelete={() => this.confirmDelete(record.id)}
              />
            </Fragment>
          ) : null,
      },
    ];
    return (
      <Table
        loading={this.state.loading}
        bordered
        dataSource={this.state.dataTable}
        columns={columns}
        pagination={this.state.pagination}
        onChange={this.handleTableChange}
      />
    );
  };

  render() {
    return (
      <Card title="Danh mục loại giấy tờ">
        <Row>
          <Col span={24}>
            <Link to={routeConstant.DANH_MUC__TUYEN_SINH__LOAI_GIAY_TO__NHAP_DU_LIEU}>
              <BtnAddNew />
            </Link>
          </Col>
        </Row>
        <Row>
          <Col span={24}>{this.renderTable()}</Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <PaperType />
  </Page>
);



